import React from "react"
import { graphql, StaticQuery } from "gatsby"

const ShowSiteMetadata = ({ data }) => {
  const title = data.site.siteMetadata.name
  const description = data.site.siteMetadata.description

  return (
    <>
      <div> {title}</div>
      <p> {description}</p>
    </>
  )
}

const Header = () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              name
              description
            }
          }
        }
      `}
      render={data => <ShowSiteMetadata data={data} />}
    />
  )
}

export default Header
